:root {
  --white: #FFFFFF;
  --brightgrey: #d3d3d3;
  --black: #000000;
  --blue: #0A05F2;
  --brightgreen: #13F000;
  --red: #ff0000;
  --background: #F9F1F0;
  --primary: #FADCD9;
  --secondary: #F8AFA6;
  --lightBlue: #D9D9D9;
  --realRed: #FF6464;
  --fontFamily: aboreto, -apple-system, Arial, Helvetica, sans-serif;
